<template lang="pug">
  div
    v-md-editor.github-markdown-body(:value="text" mode="preview")
    div.view-win
      canvas(width="1000" height="400" id="canvas-win" style="border: 1px solid #efefef;")
      div(style="margin-top: 12px")
        el-button(@click="addClickEvent" type="small") 添加点击事件
        el-button(@click="addMoveEvent" type="small") 添加鼠标移动事件
        el-button(@click="removeClickEvent" type="small") 删除事件
    v-md-editor.github-markdown-body(:value="text2" mode="preview")
</template>

<script>
import { fabric } from 'fabric';
import logo from '@/assets/images/404.jpg' // 引入图片
let canvas = null
export default {
  components: {
  },
  data() {
    return {
      text: `
  ### fabric
  > Fabric.js 是一个功能强大且操作简单的 Javascript HTML5 canvas 工具库。

  [这是官网传送门](http://fabricjs.com/)

  npm下载依赖：
  \`\`\`
  import { fabric } from 'fabric'
  \`\`\`
  #### 常用业务场景
  - 签字功能，提供空白画布支持签字，签字完序列化为图片
  #### 示例(支持画笔)
  `,
      text2: `
  [画布事件传送门](http://fabricjs.com/events)
  #### 基础使用方法说明
  \`\`\`js
  // html创建canvas
  canvas(width="1000" height="400" id="canvas-win" style="border: 1px solid #efefef;")
  // js引入
  import { fabric } from 'fabric'
  // 移除画布点击事件
  removeClickEvent() {
    canvas.off('mouse:down');
    canvas.off('mouse:move');
  },
  // 增加画布点击事件
  addClickEvent() {
    this.removeClickEvent() // 在添加事件之前先把该事件清除掉，以免重复添加
    canvas.on('mouse:down', options => {
      console.log(\`x轴坐标: options.e.clientX;    y轴坐标: options.e.clientY\`)
    })
  },
  // 增加画布鼠标移动事件
  addMoveEvent() {
    this.removeClickEvent() // 在添加事件之前先把该事件清除掉，以免重复添加
    canvas.on('mouse:move', options => {
      console.log(\`x轴坐标: options.e.clientX;    y轴坐标: options.e.clientY\`)
    })
  },
  // 创建一个长方形
  createRect() {
    const rect = new fabric.Rect({
      top: 30, // 距离容器顶部 30px
      left: 30, // 距离容器左侧 30px
      width: 100, // 宽 100px
      height: 60, // 高 60px
      fill: '#4e6beb' // 填充色
    })
    return rect;
  },
  // 绘制路径
  createPath() {
    const path = new fabric.Path('M 0 110 L 60 60 L 200 60 z')
    path.set({
      top: 30, // 距离容器顶部距离 50px
      left: 160, // 距离容器左侧距离 50px
      fill: '#4e6beb', // 填充 亮粉色
      opacity: 0.9, // 不透明度 50%
      stroke: 'black', // 描边颜色 黑色
      strokeWidth: 8 // 描边粗细 10px
    })
    return path;
  },
  // 导入图片
  createImg(canvas) {
    // 第一个参数是图片资源，可以放入本地图片，也可以放网络图片；
    // 第二个参数是回调函数，图片加载完就可以对图片对象进行操作
    // 第三个参数是通用属性设置
    fabric.Image.fromURL(logo, oImg => {
      oImg.scale(0.08) // 缩放
      canvas.add(oImg) // 将图片加入到画布
    }, {
      top: -30,
      left: 430,
    })
  },
  // 创建一个不可操作的画布 只需把 new fabric.Canvas 改成 new fabric.StaticCanvas 即可
  // 默认可拖拽
  canvas = new fabric.Canvas('canvas-win', {
    isDrawingMode: true, // 开启绘图模式
  });
  // 设置画笔颜色
  canvas.freeDrawingBrush.color = '#11999e'
  // 设置画笔粗细
  canvas.freeDrawingBrush.width = 6
  // 画笔投影
  canvas.freeDrawingBrush.shadow = new fabric.Shadow({
    blur: 10,
    offsetX: 10,
    offsetY: 10,
    affectStroke: true,
    color: '#30e3ca',
  })
  let rect = this.createRect();
  let text = this.createText();
  let path = this.createPath();
  // 在canvas画布中加入矩形（rect）
  // canvas.add(rect)
  // 在canvas画布中加入绘制路径（path）
  // canvas.add(path)
  // canvas内创建图片
  this.createImg(canvas);
  // 建组，多个元素放一起
  const group = new fabric.Group([rect, text], {
    top: 30, // 整组距离顶部30
    left: 30, // 整组距离左侧30
    angle: -10, // 整组旋转-10deg
  })
  // 控制组第一个元素rect,切换颜色
  group.item(0).set({fill: 'lightblue'})
  canvas.add(group)
  // 序列号
  console.log('canvas stringify ', JSON.stringify(canvas))
  console.log('canvas toJSON', canvas.toJSON())
  console.log('canvas toObject', canvas.toObject())
  // 在控制台输出 png（base64）。这个操作可能会打断 canvas 的渲染，所以之后要再执行以下 canvas.requestRenderAll() 。
  console.log('toPng', canvas.toDataURL('png'))
  canvas.requestRenderAll()
  console.log(canvas.toSVG()) // 输出 SVG
  // 反序列化，入参json
  // canvas.loadFromJSON(canvas.toJSON())
  \`\`\`
  **图形常用样式**
  \`\`\`
  top: 100, // 距离容器顶部距离
  left: 100, // 距离容器左侧距离
  radius: 50, // 半径：50px
  backgroundColor: 'green', // 背景色：绿色
  fill: 'orange', // 填充色：橙色
  stroke: '#f6416c', // 边框颜色：粉色
  strokeWidth: 5, // 边框粗细：5px
  strokeDashArray: [20, 5, 14], // 边框虚线规则：填充20px 空5px 填充14px 空20px 填充5px ……
  shadow: '10px 20px 6px rgba(10, 20, 30, 0.4)', // 投影：向右偏移10px，向下偏移20px，羽化6px，投影颜色及透明度
  transparentCorners: false, // 选中时，角是被填充了。true 空心；false 实心
  borderColor: '#16f1fc', // 选中时，边框颜色：天蓝
  borderScaleFactor: 5, // 选中时，边的粗细：5px
  borderDashArray: [20, 5, 10, 7], // 选中时，虚线边的规则
  cornerColor: "#a1de93", // 选中时，角的颜色是 青色
  cornerStrokeColor: 'pink', // 选中时，角的边框的颜色是 粉色
  cornerStyle: 'circle', // 选中时，叫的属性。默认rect 矩形；circle 圆形
  cornerSize: 20, // 选中时，角的大小为20
  cornerDashArray: [10, 2, 6], // 选中时，虚线角的规则
  selectionBackgroundColor: '#7f1300', // 选中时，选框的背景色：朱红
  padding: 40, // 选中时，选择框离元素的内边距：40px
  borderOpacityWhenMoving: 0.6, // 当对象活动和移动时，对象控制边界的不透明度
  angle: 30 // 旋转30度
  scaleX: 2, // x轴方向放大2倍；scale是负数时，图形会反转
  scaleY: 2 // y轴方向放大2倍；scale是负数时，图形会反转
  \`\`\`
   **文字常用样式**
  \`\`\`
  top: 40,
  left: 40,
  fontSize: 120,
  backgroundColor: 'green', // 背景色：绿色
  fill: 'orange', // 填充色：橙色
  stroke: '#f6416c', // 边框颜色：粉色
  strokeWidth: 3, // 边框粗细：3px
  strokeDashArray: [20, 5, 14], // 边框虚线规则：填充20px 空5px 填充14px 空20px 填充5px ……
  shadow: '10px 20px 6px rgba(10, 20, 30, 0.4)', // 投影：向右偏移10px，向下偏移20px，羽化6px，投影颜色及透明度
  transparentCorners: false, // 选中时，角是被填充了。true 空心；false 实心
  borderColor: '#16f1fc', // 选中时，边框颜色：天蓝
  borderScaleFactor: 5, // 选中时，边的粗细：5px
  borderDashArray: [20, 5, 10, 7], // 选中时，虚线边的规则
  cornerColor: "#a1de93", // 选中时，角的颜色是 青色
  cornerStrokeColor: 'pink', // 选中时，角的边框的颜色是 粉色
  cornerStyle: 'circle', // 选中时，叫的属性。默认rect 矩形；circle 圆形
  cornerSize: 20, // 选中时，角的大小为20
  cornerDashArray: [10, 2, 6], // 选中时，虚线角的规则
  selectionBackgroundColor: '#7f1300', // 选中时，选框的背景色：朱红
  padding: 40, // 选中时，选择框离元素的内边距：40px
  borderOpacityWhenMoving: 0.6, // 当对象活动和移动时，对象控制边界的不透明度
  \`\`\`
   **组常用方法**
  \`\`\`
  getObjects() 返回一组中所有对象的数组
  size() 所有对象的数量
  contains() 检查特定对象是否在 group 中
  item() 组中元素
  forEachObject() 遍历组中对象
  add() 添加元素对象
  remove() 删除元素对象
  fabric.util.object.clone() 克隆
  \`\`\`
  [参考文章传送门](https://juejin.cn/post/7026941253845516324#heading-99)
  `,
    };
  },
  methods: {
    // 移除画布点击事件
    removeClickEvent() {
      canvas.off('mouse:down');
      canvas.off('mouse:move');
    },
    // 增加画布点击事件
    addClickEvent() {
      this.removeClickEvent() // 在添加事件之前先把该事件清除掉，以免重复添加
      canvas.on('mouse:down', options => {
        console.log(`x轴坐标: ${options.e.clientX};    y轴坐标: ${options.e.clientY}`)
      })
    },
    // 增加画布鼠标移动事件
    addMoveEvent() {
      this.removeClickEvent() // 在添加事件之前先把该事件清除掉，以免重复添加
      canvas.on('mouse:move', options => {
        console.log(`x轴坐标: ${options.e.clientX};    y轴坐标: ${options.e.clientY}`)
      })
    },
    createRect() {
      // 创建一个长方形
      const rect = new fabric.Rect({
        top: 30, // 距离容器顶部 30px
        left: 30, // 距离容器左侧 30px
        width: 100, // 宽 100px
        height: 60, // 高 60px
        fill: '#4e6beb' // 填充色
      })
      return rect;
    },
    createText() {
      const text = new fabric.Text('这是长方形', {
        top: 50,
        left: 80,
        fontSize: 14,
        originX: "center",
        originY: "center",
        fill: '#efefef', // 填充色：橙色
      })
      return text;
    },
    createPath() {
      // 绘制路径
      const path = new fabric.Path('M 0 110 L 60 60 L 200 60 z')
      path.set({
        top: 30, // 距离容器顶部距离 50px
        left: 160, // 距离容器左侧距离 50px
        fill: '#4e6beb', // 填充 亮粉色
        opacity: 0.9, // 不透明度 50%
        stroke: 'black', // 描边颜色 黑色
        strokeWidth: 8 // 描边粗细 10px
      })
      // top: 100, // 距离容器顶部距离
      // left: 100, // 距离容器左侧距离
      // radius: 50, // 半径：50px
      // backgroundColor: 'green', // 背景色：绿色
      // fill: 'orange', // 填充色：橙色
      // stroke: '#f6416c', // 边框颜色：粉色
      // strokeWidth: 5, // 边框粗细：5px
      // strokeDashArray: [20, 5, 14], // 边框虚线规则：填充20px 空5px 填充14px 空20px 填充5px ……
      // shadow: '10px 20px 6px rgba(10, 20, 30, 0.4)', // 投影：向右偏移10px，向下偏移20px，羽化6px，投影颜色及透明度
      // transparentCorners: false, // 选中时，角是被填充了。true 空心；false 实心
      // borderColor: '#16f1fc', // 选中时，边框颜色：天蓝
      // borderScaleFactor: 5, // 选中时，边的粗细：5px
      // borderDashArray: [20, 5, 10, 7], // 选中时，虚线边的规则
      // cornerColor: "#a1de93", // 选中时，角的颜色是 青色
      // cornerStrokeColor: 'pink', // 选中时，角的边框的颜色是 粉色
      // cornerStyle: 'circle', // 选中时，叫的属性。默认rect 矩形；circle 圆形
      // cornerSize: 20, // 选中时，角的大小为20
      // cornerDashArray: [10, 2, 6], // 选中时，虚线角的规则
      // selectionBackgroundColor: '#7f1300', // 选中时，选框的背景色：朱红
      // padding: 40, // 选中时，选择框离元素的内边距：40px
      // borderOpacityWhenMoving: 0.6, // 当对象活动和移动时，对象控制边界的不透明度
      return path;
    },
    createImg(canvas) {
      // 第一个参数是图片资源，可以放入本地图片，也可以放网络图片；
      // 第二个参数是回调函数，图片加载完就可以对图片对象进行操作
      // 第三个参数是通用属性设置
      fabric.Image.fromURL(logo, oImg => {
        oImg.scale(0.08) // 缩放
        canvas.add(oImg) // 将图片加入到画布
      }, {
        top: -30,
        left: 430,
      })
    },
  },
  mounted() {
    // 创建一个不可操作的画布 只需把 new fabric.Canvas 改成 new fabric.StaticCanvas 即可
    // 默认可拖拽
    canvas = new fabric.Canvas('canvas-win', {
      isDrawingMode: true, // 开启绘图模式
    });
    // 设置画笔颜色
    canvas.freeDrawingBrush.color = '#11999e'
    // 设置画笔粗细
    canvas.freeDrawingBrush.width = 6
    // 画笔投影
    canvas.freeDrawingBrush.shadow = new fabric.Shadow({
      blur: 10,
      offsetX: 10,
      offsetY: 10,
      affectStroke: true,
      color: '#30e3ca',
    })
    let rect = this.createRect();
    let text = this.createText();
    let path = this.createPath();
    // 在canvas画布中加入矩形（rect）
    // canvas.add(rect)
    // 在canvas画布中加入文字（text）
    // canvas.add(text)
    // 在canvas画布中加入绘制路径（path）
    canvas.add(path)
    // 建组，多个元素放一起
    const group = new fabric.Group([rect, text], {
      top: 30,
      left: 30,
      angle: -10, // 整组旋转-10deg
    })
    // 控制组第一个元素rect,切换颜色
    group.item(0).set({fill: 'lightblue'})
    canvas.add(group)
    this.createImg(canvas);
    // 序列号
    // console.log('canvas stringify ', JSON.stringify(canvas))
    // console.log('canvas toJSON', canvas.toJSON())
    // console.log('canvas toObject', canvas.toObject())
    // 在控制台输出 png（base64）。这个操作可能会打断 canvas 的渲染，所以之后要再执行以下 canvas.requestRenderAll() 。
    console.log('toPng', canvas.toDataURL('png'))
    canvas.requestRenderAll()
    // console.log(canvas.toSVG()) // 输出 SVG
    // 反序列化，入参json
    // canvas.loadFromJSON(canvas.toJSON())
  }
}
</script>

